@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    padding: 0;
    margin: 0;
}

.header{
    width: 100%;
    text-align: center;
    background-color: #273140;
}

.logo{
    font-family: 'Lobster';
    font-size: 40px;
    padding: 10px;
}
.first{
    color:#fff
}
.second{
    color:#009688
}
*{
    padding: 0;
    margin: 0;
}

.footer{
    width: 100%;
    text-align: center;
    background-color: #2c2c2c;
    position: fixed;
    left: 0;
    bottom: 0;
}

.created{
    color:#fff;
    font-family: 'Lobster';
    font-size: 20px;
    padding: 10px;
}

.card{
    width: 300px;
    height: 300px;
    background-color: #7a7a7a;
    color:#fff;
    margin:0 20px;
    text-align: center;
    border-radius: 15px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
}
.card-title{
    font-family: 'Roboto';
    font-size: 20px;
    padding: 10px 0;
}
.card-btn1{
    width: 150px;
    background-color: #fff;
    color:#000;
    padding: 10px 20px;
    border-radius: 15px;
    margin: auto auto 20px auto;
}
.card-btn:hover{
    background-color: rgb(167, 167, 167);
    cursor: pointer;
}
.card-time{
    margin: 10px 0;
}
.card-text{
    width: 80%;
    margin: 0 auto;
    text-overflow: ellipsis; 
    overflow: hidden;
    word-break: break-word;
}
.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
}
.pagination{
    display: flex;
    list-style: none;
    justify-content: center;
}
.pagination button{
    padding: 10px;
    border:solid 1px #000;
    
}
.active button,.active:hover button{
    color:#000;
    background-color: rgb(104, 104, 104);
}
.pagination button:hover{
    cursor: pointer;
    background-color: rgb(160, 160, 160);
}

.main-container{
    display: flex;
    flex-direction: column;
    height: 80vh;
}
.listPost{
    flex-grow: 1;
}
.functional-container{
    display: flex;
    justify-content: space-between;
}
.functional-container a{
    margin: auto 0;
}
.input-container{
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 10px;
    border-radius: 20px;
    color:#fff;
    text-align: center;
    background-color: rgb(62, 62, 85);
}
.input{
    padding: 5px;
    margin: 10px;
}
.input-main{
    display: flex;
}
.btn{  
    height: 40px;
    font-size: 15px;  
}
.btn-search,.btn-clear{
    margin: auto;
}
.search,.clear,.post{
    border-radius: 20px;
    width: 70px;
    background-color: rgb(97, 88, 102);
    color: #fff;
}
.search:hover,.add:hover,.clear:hover,.post:hover{
    background-color: rgb(52, 49, 53);
    cursor: pointer;
}
.btn-add{
    margin: auto 0;
}
.add{
    border-radius: 20px;
    width: 100px;
    background-color: rgb(97, 88, 102);
    color: #fff;
}
.add-post_container{
    display: flex;
    justify-content: space-around;
    margin-top: 50px;
    
}
.see-post{
    width: 25%;
    text-align: center;
    word-break: break-all;
}
